      <h1>Анализатор комментариев</h1>
      <div class="form-settings">
        <div class="select-theme">
          <p class="input-link_name-input" >Выберите тематику рекламы</p>
          <div class="form-link">
            <select v-model="type">
              <option disabled selected value="">Выберите тематику рекламы</option>
              <option value="tehn">Техника</option>
              <option value="krasota">Красота</option>
            </select>
          </div>
        </div>
        <div class="input-link">
          <p class="input-link_name-input" >Укажите ссылку на пост</p>
          <div class="form-link">
            <input type="text" id="name-site" placeholder="Ссылочку плиз" v-model="linkParse" :disabled="type===''">
            <button v-if="!load" @click="$_anubis_sendLink" :disabled="type===''||linkParse===''">Погнали!</button>
          </div>
        </div>
      </div>
    </div>
    <div v-if="AllInfo">
      <div class="spinner bar" v-if="load">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
      <div id="rezult" class="rezult" v-else>
        <hr>
        <h1>Результаты анализа</h1>
        <p id="countComments" class="count-comment">Всего комментариев: {{Allcoments.NoN.count+Allcoments.None.count+Allcoments.Rel.count+Allcoments.Spam.count}}</p>
        <p id="percent"  class="count-comment">Процент релевантности: {{Math.round((Allcoments.Rel.count/(Allcoments.NoN.count+Allcoments.None.count+Allcoments.Rel.count+Allcoments.Spam.count))*100)+'%'}}</p>
        <div id="allComments" class="all-comments">
          <div id="allComments__blockGood" class="all-comments__blockGood">
            <p id="good" class="good comment">Годные: <span id="countGood" class="counter">{{Allcoments.Rel.count}}</span></p>
            <div class="blockGood__commentsGood" v-for="(comment,index) in Allcoments.Rel.comments" :key="index+'Rel'">
              <p>{{comment}}</p>
            </div>
          </div>

          <div id="allComments__blockBad" class="all-comments__blockBad">
            <p id="bad" class="bad comment">Туфта: <span id="countBad" class="counter">{{Allcoments.NoN.count}}</span></p>
            <div class="blockBad__commentsBad" v-for="(comment,index) in Allcoments.NoN.comments" :key="index+'Non'">
              <p>{{comment}}</p>
            </div>
          </div>
          <div id="allComments__blockSpam" class="all-comments__blockSpam">
            <p id="spam" class="spam comment">Спам: <span id="countSpam" class="counter">{{Allcoments.Spam.count}}</span></p>
            <div class="blockSpam__commentsSpam" v-for="(comment,index) in Allcoments.Spam.comments" :key="index+'Spam'">
              <p>{{comment}}</p>
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>
<script>

  export default {
    data(){
      return{
        load:false,
        AllInfo:false,
        type:'',
        linkParse:'',
        Allcoments:[],
      }
    },
    methods:{
      $_anubis_sendLink(){
        this.AllInfo=true;
        this.load=true;
        this.axios
            .post('https://01taxi.ru/parser', {url:this.linkParse})
            .then(res=>{
              console.log(res)
              if(!res.data.err){
                this.$_anubis_getComments(res.data);
              }else{
                this.comments=[];
                this.AllInfo=false;
                this.load=false;
                alert('Произошла ошибка, попробуйте позже')
              }
            });
      },
      $_anubis_getComments(comments){
        this.axios
            .post('http://95.181.198.233:3000/classifier/'+this.type, {comments:comments})
            .then(res=>{
              if(!res.data.err){
                this.Allcoments=res.data.result
                this.load=false;
              }else{
                this.comments=[];
                this.AllInfo=false;
                this.load=false;
                alert('Произошла ошибка, попробуйте позже')

              }
            });
      }
    }
  };
</script>

